@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
html , body {
  margin: 0%;
  padding: 0%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  background-color:  #EEF3FB;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Hide scrollbar for Chrome, Safari and Opera */
::-webkit-scrollbar {
    display: none;
  }
  
  /* Hide scrollbar for IE, Edge and Firefox */
  * {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }
.app{
    background-color:  #EEF3FB;
    height: 100%;
    width: 100%;
}
.header{
    float: left;
    height: 3.5rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
}
.logo_img{
    height: 50px;
    width:auto;
    padding-left: 20px;
}
.header_right_div{
    height: inherit;
    width: 200px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0px 20px;
    grid-gap: 20px;
    gap: 20px;
}
.analytics_btn{
        font-family:Poppins;
        font-size:unset;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: right;
        color: #132038;
        align-self: center;
}
.user_img{
    height: 40px;
    width: 40px;
    border-radius: 2em;
    align-self: center;
}
@media (max-width:800px) {
    .header{
        box-sizing:content-box;
        box-shadow: 0px 2px 10px #e4e8eb;
    }
}
@media (max-width:400px) {
    .logo_img{
        height: 30px;
    }
    .header_right_div{
        grid-gap: 5px;
        gap: 5px;
        justify-content: flex-end;
    }
    .user_img{
        height: 30px;
        width: 30px;
    }
}
.main_block{
    float: left;
    height: auto;
    width: 100%;
    /* padding: 20px; */
}
.inner_box{
    height: 6rem;
    padding: 30px;
    margin: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:white;
    border-radius: 15px;
    grid-gap: 10px;
    gap: 10px;
    box-sizing: border-box;
}
@media (max-width:800px) {
    .inner_box{
        height: auto;
        flex-direction: column;
        align-items: unset;
        padding: 5px;
        margin: 5px;
        border-radius: 10px;
        box-sizing: content-box;
        box-shadow: 0px 2px 10px #a4a8ab;
    }
}
.totalbankbalance{
    display: flex;
    justify-content:flex-start;
    flex-direction: column;
    font-family: Poppins;
    box-sizing: border-box;
}
.totalbankbalance h1, .totalbankbalance h6{
    color:#42d599;
    font-weight: 600;
    margin: 0px;
}
.currentbankbalance{
    display: flex;
    justify-content:flex-start;
    flex-direction: column;
    box-sizing: border-box;
}
.currentbankbalance h1 , .currentbankbalance h6{
    font-family: Poppins;
    font-weight: 600;
    margin: 0px;
    color: blue;
}
.income_expense_button_controls{
    display: flex;
    justify-content: space-between;
    grid-gap: 30px;
    gap: 30px;
}
.income_expense_button_controls button{
    border: 0px;
    padding: 0.5rem 2rem;
    color: white;
    border-radius: 10px;
    font-size: 16px;
}
.income_expense_button_controls .income_btn{
    background-color: #42d599;
}
.income_expense_button_controls .expense_btn{
    background-color: #eebd6a;
}
.controlbox{
    width: 100%;
    float: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 18px;
}
.controlbox div{
    display: flex;
    justify-content: space-between;
    grid-gap: 20px;
    gap: 20px;
}
.tabchanger_btn{
    font-size: smaller;
    font-family: Poppins;
    font-weight: 600;
    background-color: white;
    color: #9fb5c6;
    width: 9rem;
    padding: 8px 5px;
    border: 0px;
    border-radius: 5px;
}
.tabchanger_btn-active{
    background-color: #42d599;
    color: white;
}
.monthchanger_select{
    font-size:smaller;
    font-family: Poppins;
    font-weight: 600;
    background-color: white;
    width: 9rem;
    color: #9fb5c6;
    padding: 8px 5px;
    border: 0px;
    border-radius: 5px;
}
@media (max-width:800px) {
    .controlbox div{
        flex-direction: column;
        align-items: flex-start;
        grid-gap: 5px;
        gap: 5px;
    }
}


.contentviewer{
    float: left;
    width: 100%;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    height: auto;
    max-height: 465px;
    padding: 20px;
    grid-gap: 40px;
    gap: 40px;
}
@media (max-width:960px){
    .contentviewer{
        flex-direction: column;
        align-items: center;
        padding: 20px;
    }
}

.expensedivision{
    background-color: white;
    width: 100%;
    border-radius: 20px;
    display: inline;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    max-height: 465px;

}
.expenseheader{
    float: left;
    height: auto;
    width: 100%;
    padding: 15px 20px 10px;
    background-color: #e4e8eb;
    font-family: Poppins;
    color: #132038;
    border-top-left-radius:20px;
    border-top-right-radius:20px;
}
.expenseheader__data{
    display: flex;
    height: 40px;
    justify-content: space-between;
    align-items: center;
    font-weight: 600;
}
.expenseheader__data h4{
    margin: 0px;
    font-weight: 600;
}
.expenseheader__filter{
    height: auto;
    width: 100%;
    display:flex;
    justify-content: space-between;
    grid-gap: 2px;
    gap: 2px;
}
.expenseheader__filter input{
    height: auto;
    background-color: white;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    width: auto;
    font-size: small;
}
.expenseheader__filter input:focus{
    outline: none !important;
    color: blue;
    box-shadow: 0px 2px 5px blue;
}
@media (max-width:1250px){
    .expenseheader{
        padding-bottom: 5px;
    }
    .expenseheader__filter{
        display: inline-block;
        box-sizing: content-box;
    }
    .expenseheader__filter input{
        margin: 5px;
        width: 45%;
        float: inline-start;
    }
}
.cardcontainer{
    width: 100%;
    max-height: 345px;
    scroll-behavior:smooth;
    overflow-y: scroll;
    float: left;
    border-bottom-right-radius:20px ;
    border-bottom-left-radius:20px ;
}
.card{
    height: auto;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    border-radius: 0px;
    border-bottom: 1px solid #e4e8eb;
    font-family: Poppins;
}
.card ul{
    list-style: none;
    margin: 20px;
    padding: 0px;
}
.card__part1{
    width: 70%;
}
.card__part2{
    width: 30%;
}
.card .card__part1 ul{
    text-align: left;
}
.card__part2 ul {
    text-align: end;
}
.card__name{
    font-family: Poppins;
    font-weight: 600;
    font-size: medium;
    color: #132038;
}
.card__cat__des__date{
    font-family: Poppins;
    font-weight: normal;
    font-size: medium;
    color: #9fb5c6;
}
.card__income__amount{
    color:#42d599;
    font-weight: 600;
}
.card__expense__amount{
    color:#eebd6a;
    font-weight: 600;
}
.card .card__controls{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
    padding: 10px 20px 10px 0px;
}
.card .bi-pencil-fill{
    font-size: 20px;
    color: rgb(0, 0, 255);
}
.card .bi-trash-fill{
    font-size: 20px;
    color: red;
}
.card .bi-person-square , .card .bi-tags-fill , .card .bi-info-square-fill ,.card .bi-calendar3 , .card .mi-rs{
    font-size: 20px;
    margin: 5px;
    font-style: normal;
}
@media (max-width:1180px) {
    .card{
        flex-direction: column;
    }
    .card__part1 , .card__part2{
        width: 94%;
    }
    .card__part1 ul{
        margin-bottom: 0px;
    }
    .card__part2 ul{
        text-align: left;
        margin-top: 0px;
     }/*
    .card li{
        padding: 5px;
    } */
    .card .card__controls{
        position: absolute;
        top: 0px;
        right: -5px;
    }
    /*
    .card .bi-pencil-fill{
        float: right;
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 20px;
        color: rgb(0, 0, 255);
    }
    .card .bi-trash-fill{
        float: right;
        position: absolute;
        top: 40px;
        right: 10px;
        font-size: 20px;
        color: red;
    } */

}

.incomedivision{
    background-color: white;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 20px;
    display: inline;
    max-height: 465px;
}
.incomeheader{
    float: left;
    height: auto;
    width: 100%;
    padding: 15px 20px 10px;
    background-color: #e4e8eb;
    font-family: Poppins;
    color: #132038;
    border-top-left-radius:20px;
    border-top-right-radius:20px;
}
.incomeheader__data{
    display: flex;
    height: 40px;
    justify-content: space-between;
    align-items: center;
    font-weight: 600;
}
.incomeheader__data h4{
    margin: 0px;
    font-weight: 600;
}
.incomeheader__filter{
    height: auto;
    width: 100%;
    display:flex;
    justify-content: space-between;
    grid-gap: 5px;
    gap: 5px;
}
.incomeheader__filter input{
    height: auto;
    background-color: white;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    width: auto;
    font-size: small;
    
}
.incomeheader__filter input:focus{
    outline: none !important;
    box-shadow: 0px 2px 5px blue;
    color: blue;
}

@media (max-width:1250px){
    .incomeheader{
        padding-bottom: 5px;
    }
    .incomeheader__filter{
        display: inline-block;
